<template>
  <div class="container">
    <div class="title">
      <slot name="pageTitle"></slot>
    </div>
    <div class="main">
      <contentTab-com/>
      <el-table :data="tableData ? tableData : []"
                style="width:100%"
                :header-cell-style="tHeadBgFn"
                v-loading="loading">
        <el-table-column v-for="(item, key) in tHeadData"
                          :key="key"
                          :prop="key"
                          :label="item[0]"
                          align="center"
                          :show-overflow-tooltip="true"
                          :width="item[1]">
        </el-table-column>
        <el-table-column label="操作"
                          width="auto">
          <template slot-scope="scope">
            <el-button @click.native.prevent="deleteRow(scope.$index)"
                        type="text"
                        size="small">
              取消推荐
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination @size-change="handleSizeChange"
                      @current-change="handleCurrentChange"
                      :current-page="5"
                      :page-sizes="[5, 10, 15]"
                      :page-size="5"
                      layout="total, sizes, prev, pager, next, jumper"
                      :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { tHeadData } from '../../common/modules.js'
import ContentTab from './contentTab'
export default {
  name: 'Content',
  components: {
    'contentTab-com': ContentTab
  },
  data () {
    return {
      tHeadData
    }
  },
  mounted () {
    this.initData()
  },
  computed: {
    ...mapState({
      loading: state => state.interactionContent.loading,
      total: state => state.interactionContent.total,
      tableData: state => state.interactionContent.tableData
    })
  },
  methods: {
    ...mapActions({
      initData: 'interactionContent/getTableData',
      setOptions: 'interactionContent/setOptions'
    }),
    tHeadBgFn ({ rowIndex }) {
      if (rowIndex === 0) {
        return 'background:#F2F2F2'
      }
    },
    handleSizeChange (size) {
      this.setOptions({
        pageSize: size
      })
      this.initData()
    },
    handleCurrentChange (num) {
      this.setOptions({
        pageNum: num
      })
      this.initData()
    },
    deleteRow (num) {
      console.log(num)
    }
  }
}
</script>

<style scoped>
.teacherList {
  width: 100%;
  height: auto;
}
.title {
  padding: 10px 0;
  position: relative;
}
.title span {
  color: #758594;
  font-size: 14px;
}
.title button {
  width: 85px;
  height: 32px;
  background: #3a76e5;
  color: #fff;
  border: none;
  position: absolute;
  right: 10px;
}

.main {
  color: #333;
  position: relative;
}
.tab {
  border-bottom: 1px solid #eeeeee;
}
.tab span {
  font-size: 14px;
  color: #666;
  padding: 0 5px;
  line-height: 45px;
  display: inline-block;
  font-weight: bold;
  margin: 0 10px;
  cursor: pointer;
}
.active {
  border-bottom: 2px solid #3a76e5;
}
.main .el-pagination {
  margin: 15px auto;
  text-align: center;
}
</style>



