<template>
<div class="container">
  <el-button type="primary" @click="$router.push('typeInQuestion')">录入问答</el-button>
  <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="推荐问答" name="like"></el-tab-pane>
    <el-tab-pane label="全部问答" name="all"></el-tab-pane>
  </el-tabs>
</div>
</template>
<script>
import {mapActions} from 'vuex'
export default {
  name: 'ContentTab',
  data () {
    return {
      activeName: 'like'
    }
  },
  methods: {
    handleClick () {
      let tid = this.activeName === 'like' ? 2 : 1
      this.setOptions({
        tid
      })
      this.getTableData()
    },
    ...mapActions({
      getTableData: 'interactionContent/getTableData',
      setOptions: 'interactionContent/setOptions'
    })
  }
}
</script>

<style scoped>
.container {
  position: relative;
}
.container>.el-button {
  position: absolute;
  right: 10px;
  top: -20px;
  padding: 5px 10px;
}
</style>



